<template>
  <div id="overlay" class="overlay" tabindex="0" @keyup.left="prevMedia((id = media.id))" @keyup.right="nextMedia((id = media.id))" @keyup.esc="close">
    <div class="overlay--content">
      <div class="details">
        <div class="buttons">
          <div class="buttons--prev-next">
            <div class="prev" @click="prevMedia((id = media.id))">
              <span class="sr-only">previous</span>
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M15 6L9 12L15 18" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
              </svg>
            </div>
            <div class="next" @click="nextMedia((id = media.id))">
              <span class="sr-only">next</span>
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M9 6L15 12L9 18" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
              </svg>
            </div>
          </div>
          <div class="close" @click="close">
            <span class="sr-only">close</span>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M6.75827 17.2426L12.0009 12M17.2435 6.75736L12.0009 12M12.0009 12L6.75827 6.75736M12.0009 12L17.2435 17.2426" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
          </div>
        </div>
        <figure>
          <!-- <img :src="media.filename" :alt="media.alt" /> -->
          <img v-lazy="{ src: media.filename }" :alt="media.alt" />
          <figcaption v-if="media.caption">
            {{ media.caption }}
          </figcaption>
        </figure>
      </div>
    </div>
  </div>
</template>

<script>
// import mediaData from "../photos.json";
import { useRouter } from "vue-router";
import { onMounted } from "@vue/runtime-core";
// import _ from "lodash";

export default {
  name: "Image",

  props: {
    media: {
      type: Object,
      required: true,
    },
  },

  setup() {
    const router = useRouter();

    const close = () => {
      document.getElementById("app").className = "";
      router.push({ name: "Gallery" });
    };

    function nextMedia(id) {
      console.log("ID in: ", id);
      // let nextID = _.findIndex(mediaData, i => { return i.id === id; }) + 1
      let nextID = id + 1;
      // console.log(
      //   "Next Id: ",
      //   _.findIndex(mediaData, (i) => {
      //     return i.id === nextID;
      //   })
      // );

      if (nextID > 0) {
        router.push({
          name: "Media",
          params: { id: nextID },
        });
      }
    }

    function prevMedia(id) {
      // console.log("id in", id);
      // console.log(
        //   "prev ID: ",
      //   _.findIndex(mediaData, (i) => {
        //     return i.id === prevID;
      //   })
      // );
      let prevID = id - 1;

      if (prevID > 0) {
        router.push({
          name: "Media",
          params: { id: prevID },
        });
      }
    }

    onMounted(() => {
      // console.log("loaded")
      // sets focus on modal window so key bindings work
      document.getElementById("overlay").focus();
      document.getElementById("app").className = "openmodal";
    });

    return { prevMedia, nextMedia, close };
  },
};
</script>

