.overlay {
  background-color: rgba(0, 0, 0, 0.8);
  bottom: 0;
  box-sizing: border-box;
  contain: strict;
  font-size: 18px;
  left: 0;
  line-height: 1.5555555555555556;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 10;

  &--content {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    width: 100%;
  }

  .details {
    background: white;
  }

  .buttons {
    display: flex;
    justify-content: space-between;
    padding-bottom: 5px;
    padding-top: 5px;

    &--prev-next {
      display: flex;

      * {
        margin-left: 10px;
      }
    }

    .close {
      margin-right: 10px;
    }
  }

  figure {
    margin: 0;
    padding: 0;

    figcaption {
      margin: 0 auto;
      padding: 5px 10px;
    }
  }

  img {
    display: block;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    max-height: 85vh;
    max-width: 85vw;
    width: auto;
  }
}
</style>
